import { styled, keyframes } from 'styled-components';

import LoadingEllipsisSVG from '#assets/loading-ellipsis.svg';

const animation = keyframes`
  from {
    opacity: 1;
  }

  to {
    opacity: 0.2;
  }
`;

export const StyledSVG = styled(LoadingEllipsisSVG)`
  --animation-duration: 500ms;
  --animation-offset: 100ms;
  display: block;
  height: ${({ theme }) => theme.dimension.loadingEllipsis.height}px;
  margin: auto;

  circle {
    animation: ${animation} var(--animation-duration)
      cubic-bezier(0.75, 0, 0.25, 1) infinite alternate;
    fill: ${({ theme }) => theme.color.ink.base};

    &:nth-of-type(2) {
      animation-delay: calc(var(--animation-offset) * 1);
    }

    &:nth-of-type(3) {
      animation-delay: calc(var(--animation-offset) * 2);
    }
  }
`;
